import {formatTime} from "@/utils/Time.js"

export default {
	props:{
		'article' :{
			type: Object,
			required:true
		}
	},
	data() {
		return {
		}
	},
	methods: {
		// 去文章详情页
		toArticleDetail(articleId){
			uni.navigateTo({
				url:"/pages/detail/articleDetail?id="+articleId
			})
		},
		// 打开path页面
		open(path){
			let p = `/pages/${path}`;
			console.log(p)
			uni.navigateTo({
				url: p
			});
		}
	},
	computed:{
		convertTime:function(){
			return formatTime(new Date(this.article.createdTime));
		}
	}
}

